<template>
   <div class="shopDiv Contents" >
     <van-tabs v-model="activeTabs"  :sticky="sticky" @change="changes" :color="borderColor" title-inactive-color="#484848" title-active-color="#FF8D59" swipeable>
       <van-tab  title="我的门店" v-if="tabShow2">
         <shopLeft v-if="shopShow"></shopLeft>
       </van-tab>
       <van-tab title="下级代理商"  v-if="tabShow">
         <shopRight v-if="!shopShow"></shopRight>
       </van-tab>
     </van-tabs>
   </div>
</template>
<script>
  const shopLeft = () => import("../../dataStat/shop/shopLeft.vue");
  const shopRight = () => import("../../dataStat/shop/shopRight.vue");
  export default {
    components:{
      shopLeft,
      shopRight
    },
      data() {
        return {
          activeTabs: 0,
          shopShow:true,
          sticky:true,
          tabShow:false,
          tabShow2:false,
          borderColor:"rgba(0,0,0,0)"
        }
      },
      mounted(){
        if(this.$store.state.accountType=="1"&&parseInt(this.$store.state.childAgentCount)>0){//代理商

          this.tabShow=true;


        }

        if(parseInt(this.$store.state.storeManagerCount)>0){//门店

          this.tabShow2=true;

        }else {
          this.shopShow=false;
        }

        if(parseInt(this.$store.state.childAgentCount)>0&&parseInt(this.$store.state.storeManagerCount)>0){

          this.borderColor="rgb(255, 141, 89)";

        }
      },
    beforeCreate () {
       window.document.body
    },
      methods:{
          changes(index) {
            if (index == 1) {
              this.shopShow = false;
            } else {
              this.shopShow = true;
            }
          }
        },
    }
</script>
<style scoped>
  .Contents{
      margin-top: 100px;
  }
</style>



